.pokemon-card{
    display: flex;
    box-shadow: 4px 4px 4px rgba(0,0,0, 0.25);
    border-radius: 3px;
    .pokemon-img-container{
        padding: 0px 5px;
        .pokemon-img{
            width: 80px;
            height: 80px;
        }
    }
    .card-body{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 10px;
        flex: 1;
        .card-top{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            h3 {
                text-transform: capitalize;
            }
        }
        .card-bottom{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            .pokemon-type{
                display: flex;
                .pokemon-type-text{
                    margin-right: 10px;
                    text-transform: capitalize;
                }
                
            }
        }
    }
}