.pokedex-container{
    .header{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
    }
    .pokedex-grid{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 18px;
        padding: 0 10px;
    }
}